.campo-contenedor {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin: 4rem 2rem;
    .campo {

        label {
            display: block;
            margin: 1rem 0;
            font-weight: bold;
        }
        
        input {
            width: 100%;
            height: 4.5rem;
            padding: 1.3rem;
            border-radius: 1rem;
            font-size: 1.4rem;

            &:disabled {
                background-color: lighten(black, 25%);
                color: white;
                cursor: not-allowed;
                user-select: none;
            }
        }
    }
}

.btn-contenedor {
    margin: 0 2rem;
}