.campo-contenedor {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 4rem 2rem;
}
.campo-contenedor .campo label {
  display: block;
  margin: 1rem 0;
  font-weight: bold;
}
.campo-contenedor .campo input {
  width: 100%;
  height: 4.5rem;
  padding: 1.3rem;
  border-radius: 1rem;
  font-size: 1.4rem;
}
.campo-contenedor .campo input:disabled {
  background-color: #404040;
  color: white;
  cursor: not-allowed;
  user-select: none;
}

.btn-contenedor {
  margin: 0 2rem;
}